import { Component, OnInit } from '@angular/core';
import {ClassService} from '../../services/class.service';
import {AuthService} from '../../services/auth.service';
import {ActivityService} from '../../services/activity.service';
import {Router, ActivatedRoute} from '@angular/router';
@Component({
  selector: 'app-group',
  templateUrl: './group.component.html',
  styleUrls: ['./group.component.css']
})
export class GroupComponent implements OnInit {
activity: any;
today: number;
user: any;
add: boolean = false;
activityName: string;
htmlContent: string;
email: string;
students = [];
comments = [];
commentStr: any;
editorConfig={
    "editable": true,
    "spellcheck": true,
    "translate": "yes",
    "enableToolbar": true,
    "showToolbar": true,
    "placeholder": "Enter text here...",
    "imageEndPoint": "",
    "toolbar": [
        ["bold", "italic", "underline", "strikeThrough", "superscript", "subscript"],
        ["fontName", "fontSize", "color"],
        ["justifyLeft", "justifyCenter", "justifyRight", "justifyFull", "indent", "outdent"]]
}
  constructor(private classService:ClassService, private authService:AuthService,  private activityService:ActivityService, private route: ActivatedRoute,private router: Router) { }

  ngOnInit() {

    this.activityName = this.route.snapshot.paramMap.get('activityname');
    console.log(this.activityName);

    this.activityService.getSubmissions(this.activityName).subscribe(data=>{
      console.log(data);
      if(data!=null){
        this.comments = data;
      }
    })

  	this.email = this.authService.loadEmail();
  	this.authService.getUser(this.email).subscribe(data=>{
  		this.user = data;
  		console.log(this.user);
  	});

    this.activityService.getActivityByTitle(this.activityName).subscribe(data=>{
      console.log(data);
      this.activity = data;
      this.students = data.group;
      console.log(this.activity);
      console.log(this.students);
    });

  }

tabChange(event){
	console.log(event);
	//console.log(new Date().toJSON("yyyy/MM/dd HH"));
}
addComment(){
	this.add = true;
}
submitComment(comment){
  this.commentStr={first: this.user.first, last: this.user.last, comment: comment, time: Date.now()};
	this.comments.push(this.commentStr);
	this.htmlContent = "";
	this.add=false;
	console.log(this.comments[0]);
  this.activityService.updateSubmission(this.activityName, this.commentStr).subscribe(data=>{
    console.log(data);
  });
	//this.today = Date.now();
	//console.log(this.today);
}
cancel(){
  this.add = false;
}
clear(){
  this.htmlContent="";
}
delete(key){
   this.comments.splice(key, 1);
}
}
