import { Component, OnInit, ViewChild, TemplateRef, NgZone } from '@angular/core';
import {Router} from '@angular/router';
import {HttpClient} from '@angular/common/http';
import { map } from 'rxjs/operators';
import {ClassService} from '../../services/class.service';
import {AuthService} from '../../services/auth.service';
import { Pipe, PipeTransform } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
export var module_code = module_code;
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
newTitle: String;
newModule_code: String;
enrollCode: String;

renderer = false;
email: string;
first: string;
last: string;
title: string;
role: string;
password: string;
id: string;
public classIds=[];
public classes=[];
user: any;
show: boolean = false;
teacher: boolean = false;
modalRef: BsModalRef;
config = {
    animated: true,
    keyboard: true
  };
 class: any;
studentCount: any;
students = [];

  constructor(private _ngZone: NgZone,private modalService: BsModalService,private classService: ClassService, private authService: AuthService, private router: Router) { }

  ngOnInit() {
    this.authService.getProfile().subscribe(data=>{
      console.log(data);
    });

    this.email = this.authService.loadEmail();
    this.authService.getUser(this.email).subscribe(data =>{
      console.log(data);

      this.user = data;
      
      this.classes = data.classes;

      console.log(this.classes);
      this.first = data.first;
      this.last = data.last;

      if(this.authService.isTeacher()){
        this.teacher=true;
      }
    },
    err =>{
      console.log(err);
      return false;
    });

    console.log(this.classes);

    this.classService.getStudents().subscribe(data=>{
      this.students = data;
    });
  }

  storeCode(module_code, email){
  	this.classService.storeClassData(module_code, email);
  }

  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template, this.config);
  }

  createClass(){
    this.class = {
      title : this.newTitle,
      module_code : this.newModule_code,
      teacher: {
        email: this.email,
        first: this.user.first,
        last: this.user.last
      }
    }
    this.classService.createClass(this.class).subscribe(
      data=>{
        if(data.status == true){
          console.log(data);
          this.classes.push(this.class);
        }
        else{
          console.log(data);
        }
      }
     )}

  enroll(){
    const user_={
      first: this.first,
      last: this.last,
      email: this.email,
      password: this.password
    }
    //console.log(this.enrollModuleCode);
    this.classService.enrollClass(this.enrollCode, user_).subscribe(
      data=>{
        if(data.status == true){
          console.log(data);
          this.classService.getClassByModule(this.enrollCode).subscribe(data=>{
            if(data.status == true){
              this._ngZone.run(() => {this.classes.push(data);});
        
                  
            }
            else{
              console.log(data);
            }
          });
        }
        else{
          console.log(data);
        }
      },
      err=>{
        console.log(err);
      });
  }

}