newTask(){
  //this.taskForm.reset();
  this.done = false;
  this.makingTask = true;
  this.add = true;
  // if(this.tasks.length > 0){
  //   this.taskIndex++;
  // }
}
addTask(){
  this.taskName = this.taskForm.get('taskNameCtrl').value;
}
submitTask(){
  this.submission = true;
  this.makingTask = false;
  this.done = true;
  this.add = false
  this.taskDesc = this.htmlContent;
  console.log(this.taskForm.get('taskNameCtrl').value);
  this.taskType = this.taskForm.get("taskTypeCtrl").value;
  console.log( this.taskName);
  console.log( this.taskType);
  console.log( this.taskDesc);
  if(this.taskType =='image'){
    this.taskStr= {task_name: this.taskName, task_type: "image", task_desc: this.taskDesc, status: 'unsubmitted'};
    this.tasks.push(this.taskStr);
    const formData = new FormData();
    formData.append('file', this.file);
    formData.append('activityName',this.activityName);
    formData.append('taskName', this.taskName);
    formData.append('taskDesc', this.taskDesc);
    formData.append('taskType', this.taskType);
    formData.append('submitted', 'false');
    this.http.post('http://localhost:3000/upload/image/'+this.module_code, formData)
      .subscribe(res=>{
        console.log(res);
      });

  }
  if(this.taskType=='text'){
    this.taskStr= {task_name: this.taskName, task_type: this.taskType, task_desc: this.taskDesc, status: 'unsubmitted'};
    this.tasks.push(this.taskStr);
    this.task={
      activityName: this.activityName,
      taskName: this.taskName,
      taskDesc: this.taskDesc,
      taskType: this.taskType,
      submitted: false
    }

    this.http.post('http://localhost:3000/upload/text/'+this.module_code, this.task)
      .subscribe(res=>{
        console.log(res);
      });
  }
  console.log(this.tasks);
  this.taskForm.reset();
  this.url=null;
  this.taskName=null;
  this.taskDesc=null;
  this.taskType=null;
  this.selected=null;

}


submitActivity(){
  this.taskFiles = [];
  this.taskService.getFilesByActName(this.activityName).subscribe(data=>{
    for(var i=0; i<data.length; i++){
      this.taskFiles.push(data[i]);
    }
    this.activityCount++;
    this.activityStr={activityName: this.activityName, activityType: this.activityType, group: this.studentGroup, module_code: this.module_code, tasks: this.taskFiles, submissions: [] };
    this.activities.push(this.activityStr);
    console.log(this.activityStr);
    this.activityNameForm.reset();
    this.activityTypeForm.reset();
    this.taskForm.reset();
    this.tasks=[];
    this.taskService.submitActivity(this.activityStr).subscribe(
        data=>{
          console.log(data);
        },
        err=>{
          console.log(err);
        });
    });

}

