import { Component, OnInit, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import {ClassService} from '../../services/class.service';
import {AuthService} from '../../services/auth.service';
import {TaskService} from '../../services/task.service';
import {Router, ActivatedRoute} from '@angular/router';
import {DomSanitizer,SafeHtml,SafeUrl,SafeStyle} from '@angular/platform-browser';
import {FormGroup, FormControl} from '@angular/forms';
declare var angular: any;
declare var anno: any;
@Component({
  selector: 'app-task',
  templateUrl: './task.component.html',
  styleUrls: ['./task.component.css'],
})
export class TaskComponent implements OnInit{
teacher: Boolean=false;
filename: any;
file: any;
path: any;
url: any;
email: string;
originalName: string;
testEmails = ["email1"];
form=new FormGroup({email: new FormControl()});
@ViewChild('test') private img: ElementRef;


  constructor(private sanitize: DomSanitizer, private route: ActivatedRoute,private router: Router,private taskService: TaskService,private authService: AuthService, private classService: ClassService) { }

  ngOnInit() {
   
    const desc = this.route.snapshot.paramMap.get('title');
  	if(this.authService.isTeacher()){
      this.teacher=true;
    }
    this.taskService.getTaskByTitle(desc).subscribe(data=>{
      this.file = data[0];
      this.originalName=this.file.originalname;
      this.url = "http://localhost:3000/image/"+ this.originalName;
      console.log(this.url);
      this.path = this.sanitize.bypassSecurityTrustUrl(this.url);
    },
    err=>{
      console.log(err);
    }); 

  }

addEmail(email){
  this.email = email;
  this.testEmails.push(this.email);
  this.email="";
  console.log(this.testEmails);
  this.form.reset();

}
clear(){
  this.testEmails.length--;
  console.log(this.testEmails);
}

}
