import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { NavbarComponent } from './components/navbar/navbar.component';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';
import { HomeComponent } from './components/home/home.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { ProfileComponent } from './components/profile/profile.component';
import { AnnotateComponent } from './components/annotate/annotate.component';
import {CreateClassComponent} from './components/create-class/create-class.component';
import { ClassroomComponent } from './components/classroom/classroom.component';
import { TaskComponent } from './components/task/task.component';
import { ActivityComponent } from './components/activity/activity.component';
import { GroupComponent } from './components/group/group.component';


import {AuthGuard} from './guards/auth.guards';

const routes: Routes = [
  {path:'', component: HomeComponent},
  {path:'register', component: RegisterComponent},
  {path:'login', component: LoginComponent},
  {path:'profile', component: ProfileComponent, canActivate:[AuthGuard]},
  {path:'dashboard', component: DashboardComponent, canActivate:[AuthGuard]},
  {path:'annotate/:activityname/:title', component: AnnotateComponent},
  {path:'createclass', component: CreateClassComponent},
  {path:'classroom/:classname', component: ClassroomComponent},
  {path:'task/:title', component: TaskComponent},
  {path:'activity/:title', component: ActivityComponent},
  {path:'group/:activityname', component: GroupComponent}
  ];

@NgModule({
  imports: [RouterModule.forRoot(routes,{onSameUrlNavigation: 'reload'})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
